import { Input, Scenario, ServiceQuality } from './Model'

const waiterServiceQualityToTip: Map<ServiceQuality | null, number> = new Map([
  [ServiceQuality.Low, 0.15],
  [ServiceQuality.Medium, 0.18],
  [ServiceQuality.High, 0.22],
]);

const restaurantDeliveryServiceQualityToTip: Map<ServiceQuality | null, number> = new Map([
  [ServiceQuality.Low, 100],
  [ServiceQuality.Medium, 300],
  [ServiceQuality.High, 500],
]);

const restaurantTakeoutServiceQualityToTip: Map<ServiceQuality | null, number> = new Map([
  [ServiceQuality.Low, 0],
  [ServiceQuality.Medium, 100],
  [ServiceQuality.High, 300],
]);

const cabDriverServiceQualityToTip: Map<ServiceQuality | null, number> = new Map([
  [ServiceQuality.Low, 0.1],
  [ServiceQuality.Medium, 0.15],
  [ServiceQuality.High, 0.2],
]);

const valetServiceQualityToTip: Map<ServiceQuality | null, number> = new Map([
  [ServiceQuality.Low, 100],
  [ServiceQuality.Medium, 300],
  [ServiceQuality.High, 500],
]);

const salonServiceQualityToTip: Map<ServiceQuality | null, number> = new Map([
  [ServiceQuality.Low, 0.15],
  [ServiceQuality.Medium, 0.2],
  [ServiceQuality.High, 0.25],
]);

function undefinedToNull<T>(x: T | undefined): T | null {
  if (x === undefined)
    return null;
  else
    return x
}
  
export function tipMultiplier(input: Input) {
  switch (input.scenario) {
    case Scenario.Waiter: {
      return waiterServiceQualityToTip?.get(input.serviceQuality);
    }
    case Scenario.CabDriver: {
      return cabDriverServiceQualityToTip?.get(input.serviceQuality);
    }
    case Scenario.Salon: {
      return salonServiceQualityToTip?.get(input.serviceQuality);
    }
    default: {
      return null
    }
  }
}

export function calculateTip(input: Input) {
  let tipMultipier: number | undefined
  switch (input.scenario) {
    case Scenario.Waiter: {
      tipMultipier = waiterServiceQualityToTip?.get(input.serviceQuality);
      break;
    }
    case Scenario.RestaurantDelivery: {
      return undefinedToNull(restaurantDeliveryServiceQualityToTip?.get(input.serviceQuality));
    }
    case Scenario.RestaurantTakeout: {
      return undefinedToNull(restaurantTakeoutServiceQualityToTip?.get(input.serviceQuality));
    }
    case Scenario.CabDriver: {
      tipMultipier = cabDriverServiceQualityToTip?.get(input.serviceQuality);
      break;
    }
    case Scenario.Valet: {
      return undefinedToNull(valetServiceQualityToTip?.get(input.serviceQuality));
    }
    case Scenario.Salon: {
      tipMultipier = salonServiceQualityToTip?.get(input.serviceQuality);
      break;
    }
    default: {
      return null
    }
  }

  if ((tipMultipier !== undefined) && (input.billAmount !== null)) {
    return Math.round(tipMultipier * input.billAmount);
  } else {
    return null;
  }
}

export function shouldNotHaveBill(scenario : Scenario) {
  return (scenario === Scenario.RestaurantDelivery
          || scenario === Scenario.RestaurantTakeout
          || scenario === Scenario.Valet)
}
