import React from 'react';

import { Input, Scenario, ServiceQuality } from "./Model"
import { ResultComponent } from "./Result"
import { ScenarioComponent } from "./ScenarioComponent"
import { ServiceQualityComponent } from "./ServiceQualityComponent"
import InputBillComponent from "./InputBillComponent"
import { calculateTip } from "./TipEngine"
import './App.css';

interface PageProps {
  input: Input,
  onSelectScenario: (scenario: Scenario) => void,
  onSelectServiceQuality: (serviceQuality: ServiceQuality) => void,
  onSubmitBillAmount: (billAmount: number) => void,
}
export class Page extends React.Component<PageProps, {}> {
  render() {
    if (this.props.input.scenario === null) {
      return <ScenarioComponent onSelectScenario={(scenario) => this.props.onSelectScenario(scenario)} />
    }

    if (this.props.input.billAmount === null) {
      return <InputBillComponent onSubmit={(billAmount) => this.props.onSubmitBillAmount(billAmount)} />
    }

    if (this.props.input.serviceQuality === null) {
      return <ServiceQualityComponent onSelectServiceQuality={(serviceQuality) => this.props.onSelectServiceQuality(serviceQuality)} />
    }

    let tipAmount = calculateTip(this.props.input);

    if (tipAmount !== null) {
      return (
        <div className='result'>
          <ResultComponent
            input={this.props.input}
            billAmount={this.props.input.billAmount}
            tipAmount={tipAmount}
          />
        </div>
      )
    }
  }
};
