import React from 'react';

interface InputBillProps {
  onSubmit: (billAmount: number) => void
}

interface InputBillState {
  value: string,
}

interface BillAmountCmoponentProperties {
  onSubmit: (billAmountInCents: number) => void
};

interface BillAmountComponentState {
  value: Array<number>,
  errorMessage: null | string
}

const expectedPattern = /^([0-9]+)\.([0-9]?[0-9]?[0-9])$/

function parseBillAmountInput(userEnteredString: String) {
  const matched = userEnteredString.match(expectedPattern)

  if (matched) {
    let dollarAmount = matched[1]
    let centAmount = matched[2]
    let output = new Array(dollarAmount.length + centAmount.length)

    for (let i = 0; i < dollarAmount.length; i++) {
      output[i] = parseInt(dollarAmount[i])
    }

    for (let i = 0; i < centAmount.length; i++) {
      output[i + dollarAmount.length] = parseInt(centAmount[i])
    }

    return output
  } else {
    return null
  }
}

function isArrayEqual(a: Readonly<Array<Number>>, b: Readonly<Array<Number>>) {
  if (a.length === b.length) {
    for (let i = 0; i < a.length; i++) {
      if (a[i] !== b[i]) {
        return false
      }
    }

    return true
  } else {
    return false
  }
}

function prettifyDigits(output: Array<number>) {
  let len = output.length;
  if (len === 0) {
    return "0.00"
  } else if (len === 1) {
    return `0.0${output[0]}`
  } else if (len === 2) {
    return `0.${output[0]}${output[1]}`
  } else {
    let dollarAmount = output.slice(0, len - 2).join("")
    return `${dollarAmount}.${output[len - 2]}${output[len - 1]}`
  }
}

function stripZeroPrefix(digits: Array<number>) {
  for (let i = 0; i < digits.length; i++) {
    if (digits[i] !== 0) {
      return digits.slice(i);
    }
  }

  return []
}

function optionMap<T, S>(arg: T | null, fn: (arg: T) => S) {
  if (arg === null) {
    return null
  } else {
    return fn(arg);
  }
}

function digitsToNumber(digits: Readonly<Array<number>>) {
  let acc = 0;
  for (const digit of digits) {
    acc = (acc * 10) + digit;
  }
  return acc;
}

class BillInputComponent extends React.Component<BillAmountCmoponentProperties, BillAmountComponentState> {
  constructor(props: BillAmountCmoponentProperties) {
    super(props);
    this.state = { value: [], errorMessage: null }
  }

  handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    let oldState = this.state.value;
    let newState = optionMap(parseBillAmountInput(event.target.value), stripZeroPrefix);
    if (newState !== null && !isArrayEqual(newState, oldState)) {
      this.setState({ value: newState, errorMessage: null });
    }
  }

  handleSubmit(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
    event.preventDefault();
    let billAmountInCents = digitsToNumber(this.state.value)
    if (billAmountInCents === 0) {
      let newState = { ...this.state }
      newState.errorMessage = "Bill Amount cannot be zero!"
      this.setState(newState)
    } else {
      this.props.onSubmit(billAmountInCents);
    }
  }

  renderErrorMessage() {
    if (this.state.errorMessage !== null) {
      return <div>Oh noes :( {this.state.errorMessage}</div>
    }
  }

  render() {
    return <div>
      {this.renderErrorMessage()}
      <input value={prettifyDigits(this.state.value)} onChange={this.handleChange.bind(this)} />
      <button onClick={this.handleSubmit.bind(this)}>Submit</button>
    </div>
  }
}

class InputBillComponent extends React.Component<InputBillProps, InputBillState> {
  // eslint-disable-next-line
  constructor(props: InputBillProps) {
    super(props);
  }

  handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ value: event.target.value });
  }

  handleSubmit(amountInCents: number) {
    this.props.onSubmit(amountInCents);
  }

  render() {
    return (
      <div className='input-bill-amount'>
        <div className='input-prompt'>
          <strong>Enter bill amount</strong>
        </div>
        <div>
          <BillInputComponent
            onSubmit={this.handleSubmit.bind(this)}
          />
        </div>
      </div>
    )
  }
}

export default InputBillComponent;
