import React from 'react';

import { Input, Scenario, ServiceQuality } from "./Model"
import { Page } from "./Page"
import './App.css';

const nullInput: Input = {
  scenario: null,
  billAmount: null,
  serviceQuality: null,
}

interface AppState {
  history: Input[],
  stepNumber: number,
}
class App extends React.Component<{}, AppState> {
  constructor(props: {}) {
    super(props);
    this.state = {
      history: [nullInput],
      stepNumber: 0,
    }
  }

  handleSelectScenario(scenario: Scenario) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = { ...history[history.length - 1] }

    current.scenario = scenario;

    if (scenario === Scenario.Valet || scenario === Scenario.RestaurantDelivery || scenario === Scenario.RestaurantTakeout) {
      current.billAmount = 0;
    }

    this.setState({
      history: history.concat(current),
      stepNumber: history.length,
    });
  }

  handleSelectServiceQuality(serviceQuality: ServiceQuality) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = { ...history[history.length - 1] }

    current.serviceQuality = serviceQuality;

    this.setState({
      history: history.concat(current),
      stepNumber: history.length,
    });
  }

  handleSubmitBillAmount(billAmount: number) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = { ...history[history.length - 1] }

    current.billAmount = billAmount;

    this.setState({
      history: history.concat(current),
      stepNumber: history.length,
    });
  }

  startAgain() {
    this.setState({
      history: [nullInput],
      stepNumber: 0,
    })
  }
  getStartAgainButton() {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = { ...history[history.length - 1] }

    if (current.scenario !== null && current.billAmount !== null && current.serviceQuality !== null) {
      return (
        <button onClick={() => this.startAgain()}>Start Again</button>
      )
    } else {
      return <div></div>
    }
  }

  goBack() {
    const stepNumber = this.state.stepNumber - 1
    this.setState({
      stepNumber: stepNumber,
    })
  }
  getBackButton() {
    if (this.state.stepNumber !== 0) {
      return (
        <button onClick={() => this.goBack()}>Back</button>
      )
    } else {
      return <div></div>
    }
  }

  render() {
    const history = this.state.history;
    const current = history[this.state.stepNumber];

    return (
      <div className='app'>
        <div className='page'>
          < Page
            input={current}
            onSelectScenario={(scenario: Scenario) => this.handleSelectScenario(scenario)}
            onSelectServiceQuality={(serviceQuality: ServiceQuality) => this.handleSelectServiceQuality(serviceQuality)}
            onSubmitBillAmount={(billAmount: number) => this.handleSubmitBillAmount(billAmount)}
          />
        </div>
        <div className='navigation'>
          <div>{this.getBackButton()}</div>
          <div>{this.getStartAgainButton()}</div>
        </div>
      </div>
    )
  }
}

export default App;
