import React from 'react';
import { Input } from './Model';
import { shouldNotHaveBill, tipMultiplier } from './TipEngine';

function toDollarString(totalInCent: number): string {
  let centAmount = ("0" + (totalInCent % 100).toString()).slice(-2)
  let dollarAmount = Math.floor(totalInCent / 100).toString()
  return `$ ${dollarAmount}.${centAmount}`
}

interface ResultComponentProps {
  tipAmount: number,
  billAmount: number,
  input: Input,
};

export class ResultComponent extends React.Component<ResultComponentProps, {}> {
  // eslint-disable-next-line
  constructor(props: ResultComponentProps) {
    super(props);
  }

  tipPercentage() {
    let x = tipMultiplier(this.props.input)
    if (x === null || x === undefined) {
      return null
    }

    return (x * 100).toFixed(2)
  }

  renderTipPercentage() {
    let tipPercentage = this.tipPercentage();
    if (tipPercentage === null) {
      return null
    } else {
      return <p>Tip percentage: {tipPercentage}%</p>
    }
  }

  render() {
    let scenario = this.props.input.scenario
    if (scenario === null) {
      // eslint-disable-next-line
      throw "Assertion failure"
    }

    if (this.props.billAmount === 0 &&
      shouldNotHaveBill(scenario)) {
      return <p>Tip Amount : {toDollarString(this.props.tipAmount)}</p>

    } else {
      let totalAmount = this.props.tipAmount + this.props.billAmount
      return (
        <div>
          <p>Tip Amount : {toDollarString(this.props.tipAmount)}</p>
          {this.renderTipPercentage()}
          <p>Bill Amount: {toDollarString(this.props.billAmount)}</p>
          <p>Total : {toDollarString(totalAmount)}</p>
        </div>
      )
    }
  }
};
